body {
    background-image: url("/assets/images/bg-snow.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-attachment: fixed;
    box-sizing: border-box;
    
    font-family: 'Source Sans Pro', sans-serif;
}

a:hover, a:visited, a:link, a:active {
    text-decoration: none;
    color: black;
}

a:hover {
    color: grey;
}

h1, h2, h3, h4 {
    color:black;
}

.Title {
    text-align: center;
    font-size: 350%;
    margin: 5px;
}


.Main {
    background: rgba(255,255,255,0.95);
    border: medium solid black;
    border-radius: 10px;
    padding: 7px;
    padding-bottom: 1px;
    margin: 5px;
    line-height: 1.6;
}

nav {
    text-align: center;
    font-size: 150%;
    font-weight: bold;
}

.NavCurrentPage {
    text-decoration: underline !important;
}

.PostTitle {
    text-align: left;
    margin: 0px;
}
.PostInfo {
    text-align: right;
    margin: 0px;
}
hr {
    border: 0;
    height: 2px;
    background: #333;
}
.PostImage {
    max-width: 80%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    filter: drop-shadow(0.15rem 0.15rem 0.2rem rgba(0, 0, 0, 0.5));
}
.SmallPostImage {
    max-width: 45%;
    filter: drop-shadow(0.15rem 0.15rem 0.2rem rgba(0, 0, 0, 0.5));
}

.ImageFlexBox {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 5px;
}

.ImageFlexBox > img {
    padding: 5px;
}

.Footer {
    margin: 5px;
    text-align: center;
}

.SocialMediaIcon {
    width: 50px;
    height: auto;
    border: none;
}