/* GAMES PAGE */

/* Container for all games */
.GameContainer {
    display: flex;  
    flex-flow: row wrap;
    justify-content: center;
}

/* Display of individual game */
.GameDisplay {
    margin: 5px;
    padding: 5px;

    border: medium solid black;
    border-radius: 3px;

    width: 275px;

    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    
    text-align: center;
}

/* Title of game */
.GameTitle {
    text-align: center;

    margin: 0px;
    padding-bottom: 5px;
}

/* Image for game */
.GameImage {
    margin-left: auto;
    margin-right: auto;

    width: 250px;
    height: 250px;
}

/* Read More text */
.GameReadMore {  
    margin-top: auto;

    align-self: flex-end;
    
    text-align: right;
}

/* PAGE FOR INDIVIDUAL GAME */

/* Game title on it's own page. */
.GameInfoTitle {
    padding: 0px;
    margin: 5px;
}

.GameInfoContainer {
    display: flex;
    flex-flow: row nowrap;
    padding: 5px;
}

.GameInfoBox {
    flex-grow: 1.5;
}

.GamePostsBox {
    flex-shrink: 0;
}