.FlexContainer {
    display: flex;
    flex-direction: row;
    box-sizing: border-box;
}

.FlexItemFill {
    flex: 1 1 0;
}

.FlexItemNeed {
    flex: 1 1 1;
}

.Portrait {
    width: 300px;
    height: auto;
    margin-right: 8px;
    border-radius: 2%;
    filter: drop-shadow(0.15rem 0.15rem 0.2rem rgba(0, 0, 0, 0.5));
}